import "./styles.css";

const app = document.getElementById("app");

app.innerHTML = `
<div id="outer" style="">
  <canvas id="canvas" width="600" height="600" style="border: solid black 0px;">
    Your browser does not support canvas elements.
  </canvas>
  <div id="bottombar" style="min-width: 100%; width: 100%; min-height: 30px; height: 30px; background-color: #AAA;">
    <button id="modebutton">Draw</button>
    <button id="backgroundbutton">Empty</button>
    <button id="colorbutton">Color</button>
  </div>
</div id="outer">
<canvas id="pattern" width="50" height="50" style="display: none; left: 50px;"></canvas>
`;

var ongoingTouches = [];

var usepattern = false;
var mode = 0;
var touchstart = [];
var colorid = 0;
var colors = [
  "#000000",
  "#888888",
  "#ff0000",
  "#ff8800",
  "#ffff00",
  "#008800",
  "#00ff00",
  "#00ffff",
  "#0000ff",
  "#8800ff",
  "#ff00ff",
  "#964b00"
];

var backgroundpattern = "";

function makepattern() {
  var el = document.getElementById("pattern");
  var ctx = el.getContext("2d");
  ctx.fillStyle = "black";
  ctx.fillRect(0, 0, 50, 50);
  ctx.fillStyle = "white";
  ctx.fillRect(1, 1, 48, 48);
  backgroundpattern = ctx.createPattern(el, "repeat");
}

function clearCanvas() {
  var el = document.getElementById("canvas");
  var ctx = el.getContext("2d");
  if (usepattern) {
    ctx.fillStyle = backgroundpattern;
    ctx.fillRect(0, 0, el.width, el.height);
  } else {
    ctx.clearRect(0, 0, el.width, el.height);
  }
}

function colorForTouch(touch) {
  let color;
  color = colors[colorid];
  /*console.log(
    "color for touch with identifier " + touch.identifier + " = " + color
  );*/
  return color;
}

function copyTouch({ identifier, pageX, pageY }) {
  return { identifier, pageX, pageY };
}

function ongoingTouchIndexById(idToFind) {
  for (var i = 0; i < ongoingTouches.length; i++) {
    var id = ongoingTouches[i].identifier;

    if (id === idToFind) {
      return i;
    }
  }
  return -1; // not found
}

function handleStart(evt) {
  evt.preventDefault();
  //console.log("touchstart.");
  var el = document.getElementById("canvas");
  if (el.width !== window.innerWidth || el.height !== window.innerHeight - 30) {
    el.width = window.innerWidth.toString();
    el.style.width = window.innerWidth;
    el.height = (window.innerHeight - 30).toString();
    el.style.height = window.innerHeight - 30;
  }
  //var ctx = el.getContext("2d");
  var touches = evt.changedTouches;

  for (var i = 0; i < touches.length; i++) {
    //console.log("touchstart:" + i + "...");
    ongoingTouches.push(copyTouch(touches[i]));
    //var color = colorForTouch(touches[i]);
    touchstart[i] = touches[i];
    /*
    ctx.beginPath();
    //ctx.arc(touches[i].pageX, touches[i].pageY, 4, 0, 2 * Math.PI, false); // a circle at the start
    //ctx.fillStyle = color;
    ctx.fill();
    */
    //console.log("touchstart:" + i + ".");
  }
}

function handleMove(evt) {
  evt.preventDefault();
  var el = document.getElementById("canvas");
  var ctx = el.getContext("2d");
  var touches = evt.changedTouches;

  for (var i = 0; i < touches.length; i++) {
    var color = colorForTouch(touches[i]);
    var idx = ongoingTouchIndexById(touches[i].identifier);

    if (idx >= 0) {
      //console.log("continuing touch " + idx);
      ctx.beginPath();
      /*console.log(
        "ctx.moveTo(" +
          ongoingTouches[idx].pageX +
          ", " +
          ongoingTouches[idx].pageY +
          ");"
      );*/
      /*console.log(
        "ctx.lineTo(" + touches[i].pageX + ", " + touches[i].pageY + ");"
      );*/
      if (touches[i].force === 0) {
        ctx.lineWidth = 10;
      } else {
        ctx.lineWidth = touches[i].force * 10;
      }
      ctx.strokeStyle = color;
      ctx.lineCap = "round";
      if (mode === 0) {
        ctx.moveTo(ongoingTouches[idx].pageX, ongoingTouches[idx].pageY);
        ctx.lineTo(touches[i].pageX, touches[i].pageY);
        ctx.stroke();
      } else if (mode === 1) {
        ctx.lineWidth = 30;
        if (usepattern) {
          ctx.strokeStyle = backgroundpattern;
        } else {
          ctx.strokeStyle = "#ffffff";
        }
        ctx.moveTo(ongoingTouches[idx].pageX, ongoingTouches[idx].pageY);
        ctx.lineTo(touches[i].pageX, touches[i].pageY);
        ctx.stroke();
      }

      ongoingTouches.splice(idx, 1, copyTouch(touches[i])); // swap in the new touch record
      //console.log(".");
      /*} else {*/
      //console.log("can't figure out which touch to continue");
    }
  }
}

function handleEnd(evt) {
  evt.preventDefault();
  //console.log("touchend");
  var el = document.getElementById("canvas");
  var ctx = el.getContext("2d");
  var touches = evt.changedTouches;

  for (var i = 0; i < touches.length; i++) {
    var color = colorForTouch(touches[i]);
    var idx = ongoingTouchIndexById(touches[i].identifier);

    if (idx >= 0) {
      ctx.lineWidth = 4;
      ctx.fillStyle = color;
      //ctx.fillStyle = backgroundpattern;
      ctx.beginPath();
      if (mode === 2) {
        var radius = Math.sqrt(
          Math.pow(touchstart[i].pageX - touches[i].pageX, 2) +
            Math.pow(touchstart[i].pageY - touches[i].pageY, 2)
        );
        ctx.arc(
          touchstart[i].pageX,
          touchstart[i].pageY,
          radius,
          0,
          Math.PI * 2,
          true
        );
        ctx.stroke();
      } else if (mode === 3) {
        ctx.moveTo(touchstart[i].pageX, touchstart[i].pageY);
        ctx.lineTo(touches[i].pageX, touches[i].pageY);
        ctx.stroke();
      }
      //ctx.fillRect(touches[i].pageX - 4, touches[i].pageY - 4, 8, 8); // and a square at the end
      ongoingTouches.splice(idx, 1); // remove it; we're done
    } else {
      //console.log("can't figure out which touch to end");
    }
  }
}

function handleCancel(evt) {
  evt.preventDefault();
  //console.log("touchcancel.");
  var touches = evt.changedTouches;

  for (var i = 0; i < touches.length; i++) {
    var idx = ongoingTouchIndexById(touches[i].identifier);
    ongoingTouches.splice(idx, 1); // remove it; we're done
  }
}

function startup() {
  var el = document.getElementById("canvas");
  if (el !== null && typeof el !== "undefined") {
    document.removeEventListener("mousemove", startup);
    document.removeEventListener("touchstart", startup, false);
    makepattern();
    if (
      el.width !== window.innerWidth ||
      el.height !== window.innerHeight - 30
    ) {
      el.width = window.innerWidth.toString();
      el.style.width = window.innerWidth;
      el.height = (window.innerHeight - 30).toString();
      el.style.height = window.innerHeight - 30;
    }
    el.addEventListener("touchstart", handleStart, false);
    el.addEventListener("touchend", handleEnd, false);
    el.addEventListener("touchcancel", handleCancel, false);
    el.addEventListener("touchmove", handleMove, false);
    //window.addEventListener("keydown", onkey, false);
    document.getElementById("modebutton").addEventListener("click", (evt) => {
      var self = this.getElementById("modebutton");
      if (mode === 0) {
        self.innerHTML = "Erase";
        mode = 1;
      } else if (mode === 1) {
        self.innerHTML = "Circle";
        mode = 2;
      } else if (mode === 2) {
        self.innerHTML = "Line";
        mode = 3;
      } else if (mode === 3) {
        self.innerHTML = "Draw";
        mode = 0;
      }
    });
    document
      .getElementById("backgroundbutton")
      .addEventListener("click", (evt) => {
        var self = this.getElementById("backgroundbutton");
        if (usepattern) {
          usepattern = false;
          self.innerHTML = "No lines";
        } else {
          usepattern = true;
          self.innerHTML = "Lines";
        }
        clearCanvas();
      });
    document.getElementById("colorbutton").addEventListener("click", (evt) => {
      var self = this.getElementById("colorbutton");
      if (colorid >= colors.length) {
        colorid = 0;
      } else {
        colorid++;
      }
      self.style.background = colors[colorid];
    });
  }
}

document.addEventListener("load", () => startup);

document.addEventListener("touchstart", startup, false);
document.addEventListener("mousemove", startup);
